$font-family-primery: "Pacifico", sans-serif;
body {
  background-color: rgba(20, 255, 20, 0.7);
  font-family: $font-family-primery;
  font-size: 18px;
}
.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 5;
  background-color: rgba(200, 200, 200, 0.2);
  display: flex;
  justify-content: center;
  align-items: center;

  visibility: hidden;
  pointer-events: none;
  overflow: hidden;
}

.backdrop.visible {
  visibility: visible;
  pointer-events: all;
  overflow: visible;
}

.box-form {
  background-color: aqua;
  padding: 20px;
  width: 50%;
  height: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.stikers-wall{
  border-top: 2px green solid;
        padding: 50px;
}

.box-notice{
  display: flex;
  justify-content: start;
  align-items: center;
  flex-wrap: wrap;
  gap: 20px;
}

.stikers:nth-child(3n){
transform: rotate(0.03turn);
  background-color: aqua;
}

.stikers:nth-child(2n) {
  transform: rotate(-0.02turn);
    background-color: red;
}

.stikers:nth-child(4n) {
  transform: rotate(-0.04turn);
    background-color: blueviolet;
}

.stikers:nth-child(5n) {
  transform: rotate(-0.03turn);
  background-color: lawngreen;
}

.stikers{
  transform: rotate(0.01turn);
  background-color: hotpink;
  width: 200px;
  height: 200px;
  padding: 10px;
box-shadow: 10px 10px 8px 2px rgba(0, 0, 0, 0.3);
}

.stikers:hover {
  transform: scale(1.5);
}
